.homeWrap{
	padding: 50px;
	box-sizing: border-box;
	h2{
		text-align: center;
		padding-bottom: 30px;

	}
	.appWrap{
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		div{ padding: 10px;
			a{
				text-decoration: none;
				h2{
					font-size: 16px;
					font-weight: normal;
					color: #000;
				}
				img{height:750px; width: 500px}
			}
		}
	}
	div{
		.load{
			display: flex;
			align-items: center;
			justify-content: center;
			font-size: 40px;
			height: 70vh;
			color: red;
		}
	}
}
.upMovie{
	width: 100%;
	position: relative;
	.upBackImg{
		width: 100%;
		position: relative;
		img{
			width: 100%;
		}
	}
	.upinfoWrap{
		position: absolute;
		top: 23%;
		left: 5%;
		width: 700px;
		height: 450px;
		padding: 30px;
		background: rgba(255,255,255,.3);
		box-shadow: 0 0 30px #fff;
		display: flex;
		.upInfo{
			margin-right: 30px;
	
		}
		.upinfoText{}
	}
	
}
.more{
	width: 100%;
	text-align: center;
	.moreBtn{
		background: #0883bc;
		color: #fff;
		padding: 10px 30px;
		border: none
	}
}
